import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { Button, Image } from 'semantic-ui-react'
import MenuAdmin from '../home/MenuAdmin';

class Dashboard extends Component {

    state = {
        user:{}
    }

    componentWillMount(){
        const user = JSON.parse(localStorage.getItem('loggedUser'))
        if(!user) this.props.history.push('/login')
        else{
            this.setState({user})
        }
    }


    render(){
        const {user} = this.state
        return(
            
            <div>
                <MenuAdmin />
                <div style={{
                    textAlign:'center'
                }}>
                    <br/>
                    <br/>
                    <h1>Bienvenido, {user.name}.</h1>
                    <h2>Tu rol es administrador</h2>
                    <Image src={user.profilePic} centered size='medium' />
                    <br />
                    <h2>Selecciona abajo la opción que desea consultar</h2>
                    <div>
                        <span>
                            <Button><Link to='/employees'>Evaluadores</Link></Button>
                            
                            <h2>-</h2>
                            <Button><Link to='/results'>Bonos del mes</Link></Button>
                            
                            <h2>-</h2>
                            <Button><Link to='/contact'>Reportes</Link></Button>
                        </span>

                    </div>
                    <br />
                </div>
            </div>
        )
    }
}

export default Dashboard